"use client"

import type React from "react"
import { createContext, useContext, useState } from "react"
import { type Student, type Teacher, STUDENTS, TEACHERS } from "./dummy-data"

interface AuthContextType {
  role: "student" | "teacher" | null
  currentStudent: Student | null
  currentTeacher: Teacher | null
  login: (role: "student" | "teacher", id: string) => void
  logout: () => void
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const [role, setRole] = useState<"student" | "teacher" | null>(null)
  const [currentStudent, setCurrentStudent] = useState<Student | null>(null)
  const [currentTeacher, setCurrentTeacher] = useState<Teacher | null>(null)

  const login = (newRole: "student" | "teacher", id: string) => {
    setRole(newRole)
    if (newRole === "student") {
      const student = STUDENTS.find((s) => s.id === id)
      setCurrentStudent(student || null)
      setCurrentTeacher(null)
    } else {
      const teacher = TEACHERS.find((t) => t.id === id)
      setCurrentTeacher(teacher || null)
      setCurrentStudent(null)
    }
  }

  const logout = () => {
    setRole(null)
    setCurrentStudent(null)
    setCurrentTeacher(null)
  }

  return (
    <AuthContext.Provider value={{ role, currentStudent, currentTeacher, login, logout }}>
      {children}
    </AuthContext.Provider>
  )
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error("useAuth must be used within AuthProvider")
  }
  return context
}
